import { myreg , numTest ,setStore , getStore} from '../../common/js/test.js'
import { mapGetters } from 'vuex'
import api from '../../api/login/login.js'
export default {
	data(){
	   return{
          isActive:false,
          banktrue:true,
          bankfalse:false,
          messages:null
		 }
	},   
      computed:{
      ...mapGetters({
            banknewcard:'banknewcard',
          })
    },
   watch:{
      $route(){
        console.log(this.banknewcard == "")
        if(this.banknewcard == ""){
             return ;
        }else{
           this.banktrue = true;
        }
      },
       messages: function(newVal, oldVal){

             if(newVal.indexOf(".") > 0){
              var newT = newVal.split(".")[1];
              var newO = newVal.split(".")[0];
                 if(newT >= 2){
                    if ( newT > 2) {
                         newT = newT.substr(0, 2);
                      }
                      var value = newO+"."+newT;
                       this.messages = value;
                 }
             }
       },
    },
  created(){
    document.title="提现"
  },

  methods:{
      bank(){
        this.$router.push("/bank")
      },
      addbank(){
        this.$router.push("/adbkone")
      },
      all(){
        //获取全部的金额
      }
  }
}




